@mixin font($font-family, $font-file) {
    @font-face {
      font-family: $font-family;
      src: url($font-file+'.ttf') format('truetype');
      font-weight: normal;
      font-style: normal;
    }
}
@include font('Avenir-Black', '../fonts/Avenir-Black.ttf');
@include font('Avenir-Book', '../fonts/Avenir-Book.ttf');
@include font('Avenir-Medium', '../fonts/Avenir-Medium.ttf');


html, body, * {
    margin: 0;
    padding: 0;
    font-family: 'Avenir-Black';
    font-size: 16px;
}

header {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 6rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo a {
        background-image: url(../img/logo.png);
        background-repeat: no-repeat;
        background-size: 100%;
        height: 5rem;
        width: 12rem;
        display: block;
    }
}